<template>
  <div id="mycv">
    <h2>My Resume</h2>
    <div class="resume-container">
      <div class="official">
        <h3>Official Resume</h3>
        <div class="resumes">
          <div class="resume">
            <img :src="require(`../images/cv-${$i18n.locale}.png`)" alt />
            <div class="middle">
              <a
                class="text"
                href="https://drive.google.com/file/d/10M9Umiz3bDnjdhoFo6qeXWHuYOGatdiS/view?usp=sharing"
                target="_blank"
              >
                Download(PDF)
                <img src="../images/out-link.svg" alt />
              </a>
            </div>
          </div>
        </div>
      </div>
      <div class="casual">
        <h3>Casual Resume</h3>
        <div class="resumes">
          <div class="resume">
            <img :src="require(`../images/cv-${$i18n.locale}.png`)" alt />
            <div class="middle">
              <a
                class="text"
                href="https://drive.google.com/file/d/10M9Umiz3bDnjdhoFo6qeXWHuYOGatdiS/view?usp=sharing"
                target="_blank"
              >
                Download(PDF)
                <img src="../images/out-link.svg" alt />
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  methods: {
    fileDownload() {
      axios({
        url:
          "https://drive.google.com/file/d/1EXYmfSTLdxJLlQ4HZAfHxTBkq27Zzd_t/view?usp=sharing",
        method: "GET",
        responseType: "blob",
      }).then((response) => {
        var fileURL = window.URL.createObjectURL(new Blob([response.data]));
        var fileLink = document.createElement("a");

        fileLink.href = fileURL;
        fileLink.setAttribute("download", "file.pdf");
        document.body.appendChild(fileLink);

        fileLink.click();
      });
    },
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/_variables.scss";

h2 {
  text-align: center;
  font-size: 2.2rem;
}

.resume-container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-self: center;
  width: 90%;
  margin: 1rem auto;

  h3 {
    text-align: center;
  }

  .resumes {
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .resume {
    position: relative;
    margin: 1rem;
    max-width: 19rem;
    height: auto;
    display: flex;
    flex-direction: column;
    text-align: center;
    border-radius: 10px;
    box-shadow: 0px 4px 4px 0px rgb(226, 225, 225);
    border: 1px solid rgb(207, 207, 207);
    background-color: white;

    .middle {
      transition: 0.5s ease;
      opacity: 0;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      .text {
        background-color: $p-color;
        color: white;
        font-size: 18px;
        padding: 4px 10px 6px 15px;
        outline: none;
        border: none;
        cursor: pointer;
        border-radius: 5px;
        font-family: $s-font;
        text-decoration: none;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        img {
          width: 0.8rem;
          height: auto;
          margin-left: 10px;
          margin-bottom: 10px;
        }
      }
    }

    &:hover > img {
      opacity: 0.3;
    }

    &:hover > .middle {
      opacity: 1;
    }

    img {
      max-width: 96%;
      min-height: 86%;
      object-fit: cover;
      object-position: top;
      padding-bottom: 10px;
    }
  }
  @media #{$large_mobile, $sm_mobile} {
    display: flex;
    flex-direction: column;
    .resumes {
        .resume {
            margin-bottom: 3rem;
        }
    }
  }
}
</style>