<template>
  <div id="nav">
    <router-link :to="`/${$i18n.locale}`" active-class="active" exact>
      <a>
        <div class="logo">
          <img src="../images/logo.svg" alt="Logo Image" />
        </div>
      </a>
    </router-link>
    <ul class="navbar">
      <router-link :to="`/${$i18n.locale}`" tag="li">
        <a id="nav-link">
          <span class="underline--magical">{{ $t('nav.home')}}</span>
        </a>
      </router-link>
      <router-link :to="`/${$i18n.locale}/projects`" tag="li" active-class="active">
        <a id="nav-link">
          <span class="underline--magical">{{ $t('nav.projects')}}</span>
        </a>
      </router-link>
      <router-link :to="`/${$i18n.locale}/mycv`" tag="li" active-class="active">
        <a id="nav-link">
          <span class="underline--magical">{{ $t('nav.resume')}}</span>
        </a>
      </router-link>
      <router-link :to="`/${$i18n.locale}/contactme`" tag="li" active-class="active">
        <a id="nav-link">
          <span class="underline--magical">{{ $t('nav.contact')}}</span>
        </a>
      </router-link>
      <li>
        <div class="lang-select">
          <div class="lang-button" @click="opened = !opened">
           <img :src="require(`../images/flag_${$i18n.locale}.png`)" alt="">
          </div>
          <ul v-show="opened" v-click-outside="hide" class="lang-list">
            <li @click.prevent="setLocale('en')">
              <img class="flag" src="../images/flag_en.png" alt />
            </li>
            <li @click.prevent="setLocale('ja')">
              <img class="flag" src="../images/flag_ja.png" alt />
            </li>
            <li @click.prevent="setLocale('tr')">
              <img class="flag" src="../images/flag_tr.png" alt />
            </li>
          </ul>
        </div>
      </li>
    </ul>
  </div>
</template>


<script>
import ClickOutside from "vue-click-outside";

export default {
  name: "nav-bar",
  data: function () {
    return {
      opened: false,
    };
  },
  methods: {
    setLocale(locale) {
      this.$i18n.locale = locale;
      this.$router.push({
        params: { lang: locale },
      });
    },
    hide() {
      this.opened = false;
    },
  },
  events: {
    closeEvent: function () {
      this.hide();
    },
  },
  mounted() {
    // prevent click outside event with popupItem.
    this.popupItem = this.$el;
  },
  directives: {
    ClickOutside,
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/_variables.scss";

#nav {
  width: 100%;
  height: 6.5rem;
  border-bottom: 1px rgb(219, 217, 217) solid;
  .logo {
    position: absolute;
    left: 4rem;
    top: 10px;
    img {
      width: 5rem;
      &:hover {
        transform: scale(1.1);
        transition: all 300ms ease;
      }
    }
  }
  .underline--magical {
    background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
    background-repeat: no-repeat;
    background-size: 100% 0em;
    background-position: 0 88%;
    transition: background-size 0.15s ease-in;
    &:hover {
      background-size: 100% 100%;
    }
  }

  #nav-link {
    text-decoration: none;
    font-family: $p-font;
    font-weight: 700;
    font-size: 1.1rem;
    transition: all 0.2s ease-out;
    color: $p-color;
    &:link {
      text-decoration: none;
    }

    &:visited {
      text-decoration: none;
    }
  }
  .active {
    a {
      text-decoration: underline !important;
    }
  }
  ul {
    position: absolute;
    right: 20px;
    top: 20px;
    display: flex;
    list-style: none;
    float: right;
    align-items: center;

    li {
      margin-right: 2rem;
    }
  }

  .lang-select {
    margin-left: 1rem;
    display: flex;
    justify-content: center;
    align-content: center;
    .lang-button {
      background: none;
      border: none;
      border-radius: 6px;
      width: 3rem;
      cursor: pointer;

      img {
        width: 2.1rem;
      }
    }
  }
  .lang-list {
    width: 3rem;
    height: 6rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
    margin-right: 19.5px;
padding-right: 7px;
padding-top: 15px;
    border-top: 2px solid;
    border-color: $p-color;

    li {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
  .flag {
    width: 2rem;
    cursor: pointer;
    &:hover {
      transform: scale(1.1);
    }
  }

  /* MEDIA QURIES */

  @media #{$large_mobile, $sm_mobile} {
    .logo {
      img {
        width: 5rem;
        position: absolute;
        left: -60px;
      }
    }
    #nav-link{
      display: none;
    }
       .lang-select {
         position: absolute;
         right: 0px;
       }
       .lang-list {
         margin: 20px -10px auto auto;
       }
    
  }
}
</style> 

