<template>
  <div id="contact-form">
    <div class="from">
      <h2>{{ $t('contactForm.title') }}</h2>
      <form
        class="gform"
        id="contact"
        @submit="checkForm"
        action="https://script.google.com/macros/s/AKfycbzUH1wflCGjjdw5VeX84ontoVCQhkOcgC4znXBoQawyzkqlRKYw/exec"
        method="POST"
        novalidate="true"
      >
        <div class="upper-side">
          <p>
            <input
              id="name"
              v-model="name"
              type="name"
              name="name"
              :placeholder="$t('contactForm.name.title')"
              required
            />
            <br />
            <span id="error-message" v-show="nameErr === 'NAME'">{{ $t('contactForm.name.err') }}</span>
          </p>

          <p>
            <input
              id="email"
              v-model="email"
              type="email"
              name="email"
              :placeholder="$t('contactForm.email.title')"
              required
            />
            <br />
            <span id="error-message" v-show="mailErr === 'EMAIL'">{{ $t('contactForm.email.err') }}</span>
            <span
              id="error-message"
              v-show="mailErr === 'VEMAIL'"
            >{{ $t('contactForm.email.verr') }}</span>
          </p>
        </div>
        <p>
          <br />
          <textarea
            id="message"
            name="message"
            v-model="message"
            :placeholder="$t('contactForm.message.title')"
            required
          ></textarea>
          <br />
          <span
            id="error-message"
            v-show="messageErr === 'MESSAGE'"
          >{{ $t('contactForm.message.err') }}</span>
        </p>
        <p>
          <button id="submit" v-show="ok" type="submit">{{ $t('contactForm.buttons.send') }}</button>
          <button
            id="submit"
            type="submit"
            v-if="sender === 'SENDING'"
          >{{ $t('contactForm.buttons.sending') }}</button>
          <button
            id="submit"
            type="submit"
            v-if="sender === 'SENT'"
          >{{ $t('contactForm.buttons.sent') }}</button>
        </p>
        <h3 id="ok-message" v-show="noerrors === 'THANKS'">{{ $t('contactForm.thanks') }}</h3>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      nameErr: null,
      mailErr: null,
      messageErr: null,
      noerrors: null,
      name: null,
      email: null,
      message: null,
      submitStatus: null,
      sender: "Send",
      ok: true,
    };
  },
  methods: {
    checkForm(e) {
      if (!this.name) {
        this.nameErr = "NAME";
      }
      if (!this.email) {
        this.mailErr = "EMAIL";
      } else if (!this.validEmail(this.email)) {
        this.mailErr = "VMAIL";
      }
      if (!this.message) {
        this.messageErr = "MESSAGE";
      }

      if (!this.nameErr && !this.emailErr && !this.messageErr) {
        this.ok = false;
        this.sender = "SENDING";
        setTimeout(() => {
          this.ok = false;
          this.sender = "SENT";
          this.noerrors = "THANKS";
        }, 1500);
        return true;
      }

      e.preventDefault();
    },
    validEmail: function (email) {
      var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },
  },
};
</script>

<style lang="scss">
@import "../assets/_variables.scss";

#contact-form {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 4rem auto;
  margin-bottom: -10rem;
  z-index: 5;
  width: auto;

  .gform {
    background-color: white;
    border-radius: 10px;
    padding: 0 1rem;
    border: 1px solid;
    border-color: $p-color;
    margin: 0 auto;
  width: auto;

    .upper-side {
      display: flex;
      flex-direction: row;
      justify-content: center;
        width: auto;

    }
    #name {
      width: 23rem;
      height: 50px;
      border: none;
      border-bottom: 1px solid;
      padding: 8px;
      margin: 10px;
      border-color: $p-color;
      &::placeholder {
        font-weight: normal;
        color: rgba(0, 0, 0, 0.733);
      }
      &:focus {
        &::placeholder {
          font-size: 16px;
          color: rgba(0, 0, 0, 0.445);
        }
      }
    }
    #email {
      width: 23rem;
      height: 50px;
      border: none;
      border-bottom: 1px solid;
      padding: 8px;
      margin: 10px;
      border-color: $p-color;
      &::placeholder {
        font-weight: normal;
        color: rgba(0, 0, 0, 0.733);
      }
      &:focus {
        &::placeholder {
          font-size: 16px;
          color: rgba(0, 0, 0, 0.445);
        }
      }
    }

    #message {
      width: 100%;
      height: 200px;
      border-radius: 5px;
      border: 1px solid;
      padding: 8px;
      margin: 0 auto;
      border-color: $p-color;
      position: relative;

      &::placeholder {
        font-weight: normal;
        color: rgba(0, 0, 0, 0.733);
      }
      &:focus {
        &::placeholder {
          font-size: 16px;
          color: rgba(0, 0, 0, 0.445);
        }
      }
    }
    #submit {
      margin: 0 auto;
      border: 1px solid;
      border-color: $p-color;
      color: $black-color;
      width: 180px;
      height: 50px;
      border-radius: 6px;
      background: none;
      font-family: $p-font;
      transition: all 100ms ease-in-out;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      padding-top: 4px;

      &:hover {
        background-color: $p-color;
        color: white;
      }
    }
    #error-message {
      color: red;
      text-align: center;
      list-style: none;
      font-family: $s-font;
      margin: 0.5rem 0;
    }
    #ok-message {
      color: green;
      font-size: 1.2rem;
      list-style: none;
    }
  }
  @media #{$large_mobile, $sm_mobile} {
    .gform {
          width: 90vw;

      .upper-side {
        #name,
        #email,
        #message {
          width: 100%;
          margin: 0 auto;
        }
        flex-direction: column;
      }
      #submit {
        color: white;
        background-color: $p-color;
      }
    }

  }
}
</style>