<template>
  <div id="mobile-menu">
    <button   @click="opened = !opened" class="hamburger">
      <label :class=" { hamnimation: opened } ">
        <b>
          <i></i>
          <i></i>
          <i></i>
        </b>
      </label>
    </button>
    <transition name="fade">
      <ul v-click-outside="hide"  v-show="opened" class="mobile-menu">
        <router-link :to="`/${$i18n.locale}`" tag="li">
        <a id="nav-link">
          <span class="underline--magical">{{ $t('nav.home')}}</span>
        </a>
      </router-link>
      <router-link :to="`/${$i18n.locale}/projects`" tag="li" active-class="active">
        <a id="nav-link">
          <span class="underline--magical">{{ $t('nav.projects')}}</span>
        </a>
      </router-link>
      <router-link :to="`/${$i18n.locale}/mycv`" tag="li" active-class="active">
        <a id="nav-link">
          <span class="underline--magical">{{ $t('nav.resume')}}</span>
        </a>
      </router-link>
      <router-link :to="`/${$i18n.locale}/contactme`" tag="li" active-class="active">
        <a id="nav-link">
          <span class="underline--magical">{{ $t('nav.contact')}}</span>
        </a>
      </router-link>
      </ul>
    </transition>
  </div>
</template>

<script>
import ClickOutside from "vue-click-outside";

export default {
  data() {
    return {
      opened: false,
    };
  },
  methods: {
    setLocale(locale) {
      this.$i18n.locale = locale;
      this.$router.push({
        params: { lang: locale },
      });
    },
    hide() {
      this.opened = false;
    },
  },
  events: {
    closeEvent: function () {
      this.hide();
    },
  },
  mounted() {
    // prevent click outside event with popupItem.
    this.popupItem = this.$el;
  },
  directives: {
    ClickOutside,
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/_variables.scss";

#mobile-menu {
  display: none;
  @media #{$large_mobile, $sm_mobile} {
    display: inline-block;
  }
}
.hamburger {
  display: block;
  position: fixed;
  right: 30px;
  bottom: 30px;
  z-index: 10;
  border: 1px solid $p-color;
  background: white;
  border-radius: 5px;
  outline: none;

  //Ham icon
  label {
    margin: auto 0;
    position: relative;
    z-index: 900;
    cursor: pointer;
    border-right: none;

    b {
      width: 40px;
      height: 40px;
      display: block;
      position: relative;
      cursor: pointer;
      transition: background 200ms ease;

      i {
        display: block;
        background: $p-color;
        width: 30px;
        height: 4px;
        position: absolute;
        left: 5px;
        top: 11px;
        border-radius: 2px;
        transform-origin: 20px 4px;
        transition: transform 200ms cubic-bezier(0.09, 0.92, 0, 1.435),
          opacity 500ms ease;

        &:nth-child(2) {
          top: 18px;
        }

        &:nth-child(3) {
          transform-origin: 26px 4px;
          top: 25px;
        }
      }
    }
  }
}

.hamnimation {
  b {
    transition-delay: 100ms;

    i {
      background-color: #0acdff;

      &:nth-child(1) {
        transform: rotate(45deg) translate(5px, 9px);
      }

      &:nth-child(2) {
        opacity: 0;
      }

      &:nth-child(3) {
        transform: rotate(-45deg) translate(9px, -13px);
      }
    }
  }
}

.mobile-menu {
  position: fixed;
  right: -2px;
  bottom: 80px;
  width: 150px;
  height: 200px;
  background-color: white;
  border: 1px solid;
  border-color: $p-color;
  border-right: none;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  z-index: 9;
  transition: all 200ms ease-in-out;
  padding: 0;
  li {
    border-bottom: 1px solid;
    border-color: $p-color;
    width: 100%;
    height: 4rem;
    padding: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    &:last-child {
      border: none;
    }
  }
  a {
    text-decoration: none;
  }
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>